}
const updateStatus = ()=>{
  let jobids=[]
  let array = tableData.value
  if (!array || array.length === 0) return;

  for (let index = 0; index < array.length; index++) {
    const element = array[index];
    console.log('element',element.status, typeof element.status)
    // 主要是用来识别第一次进入时，没有转义的status
    // if (!element.status || element.status in [1, 2, 3, 4, 5]) {
      if (!element.status || [1, 2, 3, 4, 5].includes(element.status)) {
      console.log('element-status',element.status)
      jobids.push(element.id)
    }
    
    // 主要是用来识别是否触发轮询
    if (element.status === "运行中" || element.status === "等待中" || element.isStart) {
      jobids.push(element.id)
    }
  }
  if (jobids.length === 0) return;  

  getJobStatus({project_id:projectStore.currentProject?.id,job_ids:jobids,job_type:'train'}).then(res=>{
    let newdata = []
    let mapStatus = res.data?.data
    tableData.value.forEach(item=>{
      if (!item.status) {
        item['status'] = JOB_STATUS_LABEL[1]
      }
      // 只更新有状态数据的项目
      if (mapStatus && mapStatus[item.id] !== undefined) {
        item['status'] = JOB_STATUS_LABEL[mapStatus[item.id]] 
        if (ENUM_JOB_STATUS.PENDING == mapStatus[item.id]||mapStatus[item.id]==ENUM_JOB_STATUS.RUNNING){
          item['isStart'] = true
        }else{
          item['isStart'] = false
        }
      }
      
      newdata.push(item)
    })
    tableData.value = newdata
  })
}
let loadingInstance1
